$day-size: 100px;
$border-color: #000;
$day-background-color: #eee;
$font-size: 30px;
// $font-family: Arial, Helvetica, sans-serif;
$gap: 1px;
$calendar-padding: 10px;
$day-border-features: 1px solid $border-color;
$hover-offset--vertical-distance:-20px;
$hover-transition-reaction: 0.5s;
$hover-day-background-color: pink;
$month-length: 30;
$calendar-width: calc(7 * #{$day-size} + 6 * #{$gap} );
$daysData:
  "mon" 0,
  "tue" 1,
  "wed" 2,
  "thu" 3,
  "fri" 4,
  "sut" 5,
  "sun" 6;
